<script>
import {defineComponent, computed, ref, reactive, onMounted} from "vue";
import {useStore} from "vuex";
import {useRouter} from "vue-router";
import axios from "axios";
import {Field, Form} from 'vee-validate';

export default defineComponent({

  setup() {

    const store = useStore()
    const referral_code = ref()
    const confirmReferralCode = () => {
      let reference = referral_code.value
     axios.post('https://guarded-stream-16037.herokuapp.com/referenceCode' , reference , { headers: {'Content-Type': 'application/json'}}).then(response => {
       if (response.data == 'payment not found'){
         $('#notfound-modal').modal('show');
       }
       if (response.data == 'success'){
        router.push('/order-received')
       }
     })
      $('#confirmation-modal').modal('hide');
    }
    const router = useRouter()
    const isRequired = (value) => {
      return value ? true : 'This field is required';

    }

    const books = computed(() =>
        store.getters.getCartItems)

    const subtotal = computed(() =>
        store.getters.getTotalCost
    )
    const books_in_cart = computed(() =>
        store.getters.getTotalCartItems
    )
    const completePayment = () => {
      $('#payment-modal').modal('hide');
      $('#confirmation-modal').modal('show');

      // router.push("/order-received")

    }

    const formData = reactive({

      Amount: 0,
      Remarks: '',
      TransactionDesc: '',
      AccountReference: ''
    })
    const iframe_url = ref("")
    const makePayment = () => {
      // formData.Amount = subtotal.value
      formData.Amount = subtotal.value
      formData.Remarks = "cart items"
      formData.TransactionDesc = "asdf"
      formData.reference = "cart items"
      const base64 = btoa(JSON.stringify(formData));
      console.log("base64");
      console.log("base64", base64);
      console.log("Amount : ", formData.Amount)
      console.log("Remarks : ", formData.Remarks)
      console.log("TransactionDesc : ", formData.TransactionDesc)
      console.log("AccountReference : ", formData.reference)
      iframe_url.value = "https://swypepay.io/terra/#/iframe/" + base64;
    }
    const billingformData = reactive({
      firstname: '',
      lastname: '',
      country: '',
      street: '',
      email: '',
      phone: '',
      town: '',
      subtotal: subtotal
    })
    const closeModal = () => {
      $('#payment-modal').modal('hide');
      // cash("#payment-modal").modal("hide");
    }
   const closeErrorModal = () => {
      $('#error-modal').modal('hide');
    }
    const closeNotFound = () => {
      $('#notfound-modal').modal('hide');
    }
    const showPaymentModal = () => {
      makePayment()
      console.log(billingformData)
      axios.post('https://guarded-stream-16037.herokuapp.com/halfprice', billingformData, {headers: {'Content-Type': 'application/json'}}).then(
          response => {
            console.log(response.data)
          }
      )
      if (billingformData.subtotal && billingformData.lastname && billingformData.firstname && billingformData.street && billingformData.phone && billingformData.email && billingformData.country) {
        $('#payment-modal').modal('show');
      } else {
        $('#error-modal').modal('show')
      }

    }
    const checkoutDetails = () => {
      axios.post('http://127.0.0.1:5000', formData).then(
          response => {
            console.log(response.data)
          }
      )
    }

    const goToCart = () => {
      router.push("/halfprice_cart")
    }
    const goToShop = () => {
      router.push("/halfprice_shop")
    }
    const goToHome = () => {
      router.push("/")
    }
    const goToAbout = () => {
      router.push("/halfprice_about")
    }
    const goToContact = () => {
      router.push("/halfprice_contact")
    }
    onMounted(() => {
      if (localStorage.getItem('reloaded')) {
        // The page was just reloaded. Clear the value from local storage
        // so that it will reload the next time this page is visited.
        localStorage.removeItem('reloaded');
      } else {
        // Set a flag so that we know not to reload the page twice.
        localStorage.setItem('reloaded', '1');
        location.reload();
      }
      billingformData.firstname = ''
      billingformData.phone = ''
      billingformData.lastname = ''
      billingformData.country = ''
      billingformData.town = ''
      billingformData.email = ''

    })

    return {
      store,
      router,
      books,
      subtotal,
      books_in_cart,
      completePayment,
      formData,
      iframe_url,
      makePayment,
      closeModal,
      showPaymentModal,
      checkoutDetails,
      billingformData,
      referral_code,
      isRequired,
      confirmReferralCode,
      closeErrorModal,
      closeNotFound,
      goToHome,
      goToShop,
      goToCart,
      goToAbout,
      goToContact,


    }
  }

})
</script>